.emoji_copy textarea{
    width: 100%;
    min-height: 50px;
    border-radius: 5px;
    resize: none;
    border: 1px solid #dddddd;
    padding: 10px;
    -webkit-transition: 300ms;
    -o-transition: 300ms;
    transition: 300ms;
    margin-bottom: 4px;
    font-size: 18px;
}

.emoji_copy textarea:focus,
.emoji_copy textarea:focus,
.emoji_copy textarea:active{
    outline: none;
    border-color: #523ee8;
}

.emoji_copy button{
    padding: 5px 10px;
    border: 1px solid #f3f3f3;
    text-transform: uppercase;
    border-radius: 5px;
    background-image: -webkit-gradient(linear,left top, left bottom,from(#523ee8),to(#6d47d9));
    background-image: -webkit-linear-gradient(top,#523ee8,#6d47d9);
    background-image: -o-linear-gradient(top,#523ee8,#6d47d9);
    background-image: linear-gradient(180deg,#523ee8,#6d47d9);
    color: #ffffff;
    font-size: 10px;
    cursor: pointer;
}

.emoji_copy button.close_btn{
    background: #ff0000;
    background-image: -webkit-gradient(linear,left top, left bottom,from(#D40D12),to(#94090D));
    background-image: -webkit-linear-gradient(top,#D40D12,#94090D);
    background-image: -o-linear-gradient(top,#D40D12,#94090D);
    background-image: linear-gradient(180deg,#D40D12,#94090D);
}

.emoji_copy button:focus,
.emoji_copy button:focus,
.emoji_copy button:active{
    outline: none;
}


